import React, { useState } from 'react';
import { FiBell } from 'react-icons/fi';

const NotificationCenter = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [notifications] = useState([
    {
      id: 1,
      type: 'overdue',
      company: 'Tech Solutions Inc.',
      method: 'Email',
      dueDate: '2024-01-10'
    },
    {
      id: 2,
      type: 'due',
      company: 'Digital Innovations',
      method: 'LinkedIn Message',
      dueDate: '2024-01-15'
    }
    // Add more notifications
  ]);

  const overdueCount = notifications.filter(n => n.type === 'overdue').length;
  const dueCount = notifications.filter(n => n.type === 'due').length;

  return (
    <div className="relative">
      <button
        onClick={() => setIsOpen(!isOpen)}
        className="relative p-2 text-gray-600 hover:text-gray-900 focus:outline-none"
      >
        <FiBell className="h-6 w-6" />
        {(overdueCount + dueCount) > 0 && (
          <span className="absolute top-0 right-0 inline-flex items-center justify-center px-2 py-1 text-xs font-bold leading-none text-white transform translate-x-1/2 -translate-y-1/2 bg-red-600 rounded-full">
            {overdueCount + dueCount}
          </span>
        )}
      </button>

      {isOpen && (
        <div className="absolute right-0 mt-2 w-80 bg-white rounded-lg shadow-lg ring-1 ring-black ring-opacity-5">
          <div className="p-4">
            <h3 className="text-lg font-medium text-gray-900 mb-4">Notifications</h3>

            {/* Overdue Communications */}
            {overdueCount > 0 && (
              <div className="mb-4">
                <h4 className="text-sm font-medium text-red-600 mb-2">
                  Overdue Communications ({overdueCount})
                </h4>
                {notifications
                  .filter(n => n.type === 'overdue')
                  .map(notification => (
                    <div
                      key={notification.id}
                      className="p-3 bg-red-50 rounded-md mb-2"
                    >
                      <p className="text-sm font-medium text-gray-900">
                        {notification.company}
                      </p>
                      <p className="text-sm text-gray-500">
                        {notification.method} - Due: {notification.dueDate}
                      </p>
                    </div>
                  ))}
              </div>
            )}

            {/* Due Today */}
            {dueCount > 0 && (
              <div>
                <h4 className="text-sm font-medium text-yellow-600 mb-2">
                  Due Today ({dueCount})
                </h4>
                {notifications
                  .filter(n => n.type === 'due')
                  .map(notification => (
                    <div
                      key={notification.id}
                      className="p-3 bg-yellow-50 rounded-md mb-2"
                    >
                      <p className="text-sm font-medium text-gray-900">
                        {notification.company}
                      </p>
                      <p className="text-sm text-gray-500">
                        {notification.method} - Due: {notification.dueDate}
                      </p>
                    </div>
                  ))}
              </div>
            )}

            {overdueCount + dueCount === 0 && (
              <p className="text-sm text-gray-500">No pending notifications</p>
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default NotificationCenter; 